/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import { format } from 'date-fns';
import { sentenceCase, capitalCase } from 'change-case';

// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    Avatar,
    Button,
    Checkbox,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Switch,
    Tabs,
    Tab,
    Tooltip,
    IconButton,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Paper,
    Box,
    Stack,
    Divider,
    CircularProgress,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import { UserListHead, UserListToolbar } from '../../sections/admin/user/list';

// axios
import axios from '../../utils/axios';
import { HOST_API } from '../../config';

// ----------------------------------------------------------------------

const getStorageUrl = (path) => {
    if (!path) return '';
    if (path.startsWith('http')) return path;
    const baseUrl = HOST_API.replace('/api', '');
    return `${baseUrl}/storage/${path}`;
};

const TABLE_HEAD = [
    { id: 'name', label: 'User', alignRight: false },
    { id: 'user_type', label: 'Type', alignRight: false },
    { id: 'id_type', label: 'ID Type', alignRight: false },
    { id: 'id_number', label: 'ID Number', alignRight: false },
    { id: 'provider', label: 'Provider', alignRight: false },
    { id: 'created_at', label: 'Submitted At', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'action', label: 'Action', alignRight: true },
];

const STATUS_TABS = [
    { value: 'ALL', label: 'ALL' },
    { value: 'pending', label: 'Pending' },
    { value: 'verified', label: 'Verified' },
    { value: 'rejected', label: 'Rejected' },
];

// ----------------------------------------------------------------------

export default function UserKyc() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const [kycList, setKycList] = useState([]);
    const [page, setPage] = useState(0);
    const [load, setLoad] = useState(true);
    const [totalRecords, setTotalRecords] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [dense, setDense] = useState(false);
    const [status, setStatus] = useState('ALL');
    const { enqueueSnackbar } = useSnackbar();
    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        initialize(page, rowsPerPage, status, filterName);
    }, []);

    const initialize = async (pag, limit = 5, statusVal, search) => {
        const api_page = pag + 1;
        setLoad(true);
        try {
            const response = await axios.get(
                `/api/system/all/user/kyc/records/${AccessToken}/secure?page=${api_page}&habukhan=${limit}&status=${statusVal}&search=${search}`
            );
            setKycList(response.data?.all_kyc?.data || []);
            setTotalRecords(response.data?.all_kyc?.total || 0);
            setLoad(false);
            setPage(pag);
        } catch (error) {
            setLoad(false);
            setKycList([]);
            enqueueSnackbar('Failed to fetch KYC records', { variant: 'error' });
        }
    };

    const handleChangeRowsPerPage = (event) => {
        const newLimit = parseInt(event.target.value, 10);
        setRowsPerPage(newLimit);
        setPage(0);
        initialize(0, newLimit, status, filterName);
    };

    const handleFilterByName = (name) => {
        setFilterName(name);
        setPage(0);
        initialize(0, rowsPerPage, status, name);
    };

    const handleApprove = (kycId) => {
        swal({
            title: "Confirm Approval",
            text: "Are you sure you want to approve this KYC submission?",
            icon: "warning",
            buttons: true,
            dangerMode: false,
        }).then((willApprove) => {
            if (willApprove) {
                axios.post(`/api/system/admin/kyc/approve/${AccessToken}/secure`, { kyc_id: kycId })
                    .then((res) => {
                        enqueueSnackbar(res.data.message);
                        initialize(page, rowsPerPage, status, filterName);
                    })
                    .catch((err) => {
                        enqueueSnackbar(err.message || 'Error approving KYC', { variant: 'error' });
                    });
            }
        });
    };

    const handleReject = (kycId) => {
        swal({
            title: "Confirm Rejection",
            text: "Are you sure you want to reject this KYC submission?",
            icon: "error",
            buttons: true,
            dangerMode: true,
        }).then((willReject) => {
            if (willReject) {
                axios.post(`/api/system/admin/kyc/reject/${AccessToken}/secure`, { kyc_id: kycId })
                    .then((res) => {
                        enqueueSnackbar(res.data.message);
                        initialize(page, rowsPerPage, status, filterName);
                    })
                    .catch((err) => {
                        enqueueSnackbar(err.message || 'Error rejecting KYC', { variant: 'error' });
                    });
            }
        });
    };

    const isNotFound = kycList.length === 0 && !load;

    const [openDetails, setOpenDetails] = useState(false);
    const [selectedKyc, setSelectedKyc] = useState(null);

    const handleViewDetails = (kyc) => {
        setSelectedKyc(kyc);
        setOpenDetails(true);
    };

    const handleCloseDetails = () => {
        setOpenDetails(false);
        setSelectedKyc(null);
    };

    const handleDelete = async (id) => {
        swal({
            title: 'Are you sure?',
            text: 'Once deleted, you will not be able to recover this KYC record!',
            icon: 'warning',
            buttons: true,
            dangerMode: true,
        }).then(async (willDelete) => {
            if (willDelete) {
                setLoad(true);
                try {
                    const response = await axios.post(
                        `/api/system/admin/kyc/delete/${AccessToken}/secure`,
                        { kyc_id: id }
                    );
                    if (response.data.status === 'success') {
                        enqueueSnackbar(response.data.message, { variant: 'success' });
                        initialize(page, rowsPerPage, status, filterName);
                    } else {
                        enqueueSnackbar(response.data.message, { variant: 'error' });
                    }
                } catch (error) {
                    enqueueSnackbar('Error deleting KYC', { variant: 'error' });
                }
                setLoad(false);
            }
        });
    };

    return (
        <Page title="User: KYC Management">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="User KYC Management"
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.root },
                        { name: 'Users', href: PATH_ADMIN.user.all_user },
                        { name: 'KYC Management' },
                    ]}
                />

                <Card>
                    <Tabs
                        value={status}
                        scrollButtons="auto"
                        variant="scrollable"
                        allowScrollButtonsMobile
                        onChange={(e, value) => {
                            setStatus(value);
                            initialize(0, rowsPerPage, value, filterName);
                            setPage(0);
                        }}
                    >
                        {STATUS_TABS.map((tab) => (
                            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                        ))}
                    </Tabs>

                    <Divider />

                    <UserListToolbar
                        numSelected={0}
                        filterName={filterName}
                        onFilterName={handleFilterByName}
                    />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table size={dense ? 'small' : 'medium'}>
                                <UserListHead
                                    headLabel={TABLE_HEAD}
                                    rowCount={kycList.length}
                                    numSelected={0}
                                />
                                {!load ? (
                                    <TableBody>
                                        {kycList.map((row) => {
                                            const {
                                                id,
                                                name,
                                                username,
                                                display_user,
                                                user_type,
                                                profile_image,
                                                id_type,
                                                id_number,
                                                provider,
                                                status: kycStatus,
                                                created_at,
                                                address,
                                                id_card_path,
                                                utility_bill_path,
                                            } = row;

                                            return (
                                                <TableRow hover key={id} tabIndex={-1}>
                                                    <TableCell padding="checkbox">
                                                        <Checkbox />
                                                    </TableCell>
                                                    <TableCell align="left" sx={{ minWidth: 160 }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                                            <Avatar alt={username} src={profile_image} sx={{ mr: 2 }} />
                                                            <Typography variant="subtitle2" noWrap>
                                                                @{username}
                                                            </Typography>
                                                        </Box>
                                                    </TableCell>
                                                    <TableCell align="left">
                                                        <Label color="info" variant="outline">
                                                            {sentenceCase(user_type || 'User')}
                                                        </Label>
                                                    </TableCell>
                                                    <TableCell align="left">{id_type}</TableCell>
                                                    <TableCell align="left">{id_number}</TableCell>
                                                    <TableCell align="left">{provider}</TableCell>
                                                    <TableCell align="left">{created_at && format(new Date(created_at), 'dd MMM yyyy p')}</TableCell>
                                                    <TableCell align="left">
                                                        <Label
                                                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                            color={
                                                                (kycStatus === 'verified' && 'success') ||
                                                                (kycStatus === 'pending' && 'warning') ||
                                                                'error'
                                                            }
                                                        >
                                                            {sentenceCase(kycStatus)}
                                                        </Label>
                                                    </TableCell>
                                                    <TableCell align="right">
                                                        <Stack direction="row" spacing={0.5} justifyContent="flex-end">
                                                            <Tooltip title="View Details">
                                                                <IconButton color="primary" onClick={() => handleViewDetails(row)}>
                                                                    <Iconify icon={'eva:eye-fill'} />
                                                                </IconButton>
                                                            </Tooltip>

                                                            {kycStatus === 'pending' && (
                                                                <>
                                                                    <Tooltip title="Approve">
                                                                        <IconButton color="success" onClick={() => handleApprove(id)}>
                                                                            <Iconify icon={'eva:checkmark-circle-2-fill'} />
                                                                        </IconButton>
                                                                    </Tooltip>
                                                                    <Tooltip title="Reject">
                                                                        <IconButton color="error" onClick={() => handleReject(id)}>
                                                                            <Iconify icon={'eva:close-circle-fill'} />
                                                                        </IconButton>
                                                                    </Tooltip>
                                                                </>
                                                            )}

                                                            <Tooltip title="Delete Record">
                                                                <IconButton color="error" onClick={() => handleDelete(id)}>
                                                                    <Iconify icon={'eva:trash-2-outline'} />
                                                                </IconButton>
                                                            </Tooltip>
                                                        </Stack>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                ) : (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={8} sx={{ py: 3 }}>
                                                <Typography variant="body2">Loading ...</Typography>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={8} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <FormControlLabel
                        control={<Switch checked={dense} onChange={(e) => setDense(e.target.checked)} />}
                        label="Dense padding"
                        sx={{ pl: 2 }}
                    />

                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25, 100]}
                        component="div"
                        count={totalRecords}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, pag) => initialize(pag, rowsPerPage, status, filterName)}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </Card>
            </Container>

            {/* Details Dialog */}
            <Dialog open={openDetails} onClose={handleCloseDetails} fullWidth maxWidth="sm">
                <DialogTitle>KYC Details - @{selectedKyc?.username}</DialogTitle>
                <DialogContent dividers>
                    <Stack spacing={2} alignItems="center" sx={{ mb: 3 }}>
                        <Avatar
                            alt={selectedKyc?.username}
                            src={selectedKyc?.profile_image}
                            sx={{ width: 80, height: 80, mb: 1 }}
                        />
                        <Typography variant="h6">@{selectedKyc?.username}</Typography>
                    </Stack>
                    <Stack spacing={2}>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">Status</Typography>
                            <Label color={(selectedKyc?.status === 'pending' && 'warning') || (selectedKyc?.status === 'rejected' && 'error') || 'success'}>
                                {selectedKyc?.status}
                            </Label>
                        </Box>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">ID Type</Typography>
                            <Typography variant="body2" sx={{ textTransform: 'uppercase' }}>{selectedKyc?.id_type}</Typography>
                        </Box>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">ID Number</Typography>
                            <Typography variant="body2">{selectedKyc?.id_number}</Typography>
                        </Box>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">Provider</Typography>
                            <Typography variant="body2" sx={{ textTransform: 'capitalize' }}>{selectedKyc?.provider}</Typography>
                        </Box>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">Date Submitted</Typography>
                            <Typography variant="body2">{selectedKyc?.created_at && format(new Date(selectedKyc.created_at), 'dd MMM yyyy p')}</Typography>
                        </Box>
                        <Box display="flex" justifyContent="space-between">
                            <Typography variant="overline">Address</Typography>
                            <Typography variant="body2" sx={{ textAlign: 'right', maxWidth: '70%' }}>{selectedKyc?.address || 'N/A'}</Typography>
                        </Box>

                        <Divider sx={{ my: 1 }} />
                        <Typography variant="overline">Documents</Typography>
                        <Stack direction="row" spacing={2}>
                            {selectedKyc?.id_card_path && (
                                <Box flex={1}>
                                    <Typography variant="caption" display="block" gutterBottom>I.D. Card</Typography>
                                    <Paper
                                        component="img"
                                        src={getStorageUrl(selectedKyc.id_card_path)}
                                        sx={{
                                            width: '100%',
                                            height: 120,
                                            objectFit: 'cover',
                                            cursor: 'pointer',
                                            borderRadius: 1,
                                            border: '1px solid',
                                            borderColor: 'divider'
                                        }}
                                        onClick={() => window.open(getStorageUrl(selectedKyc.id_card_path), '_blank')}
                                    />
                                </Box>
                            )}
                            {selectedKyc?.utility_bill_path && (
                                <Box flex={1}>
                                    <Typography variant="caption" display="block" gutterBottom>Utility Bill</Typography>
                                    <Paper
                                        component="img"
                                        src={getStorageUrl(selectedKyc.utility_bill_path)}
                                        sx={{
                                            width: '100%',
                                            height: 120,
                                            objectFit: 'cover',
                                            cursor: 'pointer',
                                            borderRadius: 1,
                                            border: '1px solid',
                                            borderColor: 'divider'
                                        }}
                                        onClick={() => window.open(getStorageUrl(selectedKyc.utility_bill_path), '_blank')}
                                    />
                                </Box>
                            )}
                        </Stack>

                        {selectedKyc?.full_response_json && (
                            <Box sx={{ mt: 2 }}>
                                <Typography variant="overline">Raw Verification Data</Typography>
                                <Paper sx={{ p: 1.5, bgcolor: 'background.neutral', mt: 0.5, maxHeight: 200, overflow: 'auto' }}>
                                    <pre style={{ margin: 0, fontSize: '11px', whiteSpace: 'pre-wrap', fontFamily: 'monospace' }}>
                                        {(() => {
                                            try {
                                                const raw = typeof selectedKyc.full_response_json === 'string'
                                                    ? JSON.parse(selectedKyc.full_response_json)
                                                    : selectedKyc.full_response_json;
                                                return JSON.stringify(raw, null, 2);
                                            } catch (e) {
                                                return String(selectedKyc.full_response_json);
                                            }
                                        })()}
                                    </pre>
                                </Paper>
                            </Box>
                        )}
                    </Stack>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseDetails}>Close</Button>
                </DialogActions>
            </Dialog>

            {load && (
                <Box
                    sx={{
                        position: 'fixed',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        bgcolor: 'rgba(255, 255, 255, 0.7)',
                        display: 'flex',
                        zIndex: 9999,
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}
                >
                    <CircularProgress />
                </Box>
            )}
        </Page>
    );
}
